import React, { PureComponent } from 'react';
import {
  Label,
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ReferenceArea,
  ResponsiveContainer,
} from 'recharts';

const initialData = [
  {
    name: 'Q1 2020',
    gdp: 21000,
    inflation: 2.5,
    unemployment: 4.0,
    consumerSpending: 15000,
    event: 'Economic stimulus package announced',
  },
  {
    name: 'Q2 2020',
    gdp: 20500,
    inflation: 3.0,
    unemployment: 6.0,
    consumerSpending: 14000,
    event: 'Lockdown due to pandemic',
  },
  {
    name: 'Q3 2020',
    gdp: 22000,
    inflation: 1.8,
    unemployment: 5.5,
    consumerSpending: 16000,
    event: 'Partial reopening of economy',
  },
  {
    name: 'Q4 2020',
    gdp: 23000,
    inflation: 2.2,
    unemployment: 5.0,
    consumerSpending: 17000,
    event: 'Vaccine rollout begins',
  },
  {
    name: 'Q1 2021',
    gdp: 24000,
    inflation: 2.0,
    unemployment: 4.5,
    consumerSpending: 18000,
    event: 'Economic recovery continues',
  },
  {
    name: 'Q2 2021',
    gdp: 25000,
    inflation: 1.5,
    unemployment: 4.0,
    consumerSpending: 19000,
    event: 'Government infrastructure investment',
  },
  {
    name: 'Q3 2021',
    gdp: 26000,
    inflation: 1.7,
    unemployment: 3.8,
    consumerSpending: 20000,
    event: 'Consumer spending increases',
  },
];

const getAxisYDomain = (from, to, ref, offset) => {
  const refData = initialData.slice(from - 1, to);
  let bottom = refData[0][ref];
  let top = refData[0][ref];
  refData.forEach((d) => {
    if (d[ref] > top) {
      top = d[ref];
    }
    if (d[ref] < bottom) {
      bottom = d[ref];
    }
  });

  return [(bottom | 0) - offset, (top | 0) + offset];
};

const initialState = {
  data: initialData,
  left: 'dataMin',
  right: 'dataMax',
  refAreaLeft: '',
  refAreaRight: '',
  top: 'dataMax+1',
  bottom: 'dataMin-1',
  top2: 'dataMax+20',
  bottom2: 'dataMin-20',
  animation: true,
};

const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    const gdp = payload.find(p => p.dataKey === 'gdp').value;
    const inflation = payload.find(p => p.dataKey === 'inflation').value;
    const unemployment = payload.find(p => p.dataKey === 'unemployment').value;
    const consumerSpending = payload.find(p => p.dataKey === 'consumerSpending').value;
    const event = payload[0].payload.event;

    return (    
      <div className="custom-tooltip">
        <p className="label">{`${label}`}</p>
        <p className="intro">{`Inflation: ${inflation}%`}</p>
        <p className="desc">{`Event: ${event}`}</p>
      </div>
    );
  }

  return null;
};

export default class EconomicDataChart extends PureComponent {
  constructor(props) {
    super(props);
    this.state = initialState;
  }

  zoom() {
    let refAreaLeft = this.state.refAreaLeft;
    let refAreaRight = this.state.refAreaRight;
    const data = this.state.data;

    if (refAreaLeft === refAreaRight || refAreaRight === '') {
      this.setState(() => ({
        refAreaLeft: '',
        refAreaRight: '',
      }));
      return;
    }

    if (refAreaLeft > refAreaRight) {
      const temp = refAreaLeft;
      refAreaLeft = refAreaRight;
      refAreaRight = temp;
    }

    const yDomain = getAxisYDomain(refAreaLeft, refAreaRight, 'gdp', 1000);
    const bottom = yDomain[0];
    const top = yDomain[1];

    const yDomain2 = getAxisYDomain(refAreaLeft, refAreaRight, 'inflation', 1);
    const bottom2 = yDomain2[0];
    const top2 = yDomain2[1];

    this.setState(() => ({
      refAreaLeft: '',
      refAreaRight: '',
      data: data.slice(),
      left: refAreaLeft,
      right: refAreaRight,
      bottom: bottom,
      top: top,
      bottom2: bottom2,
      top2: top2,
    }));
  }

  zoomOut() {
    const data = this.state.data;
    this.setState(() => ({
      data: data.slice(),
      refAreaLeft: '',
      refAreaRight: '',
      left: 'dataMin',
      right: 'dataMax',
      top: 'dataMax+1',
      bottom: 'dataMin',
      top2: 'dataMax+50',
      bottom2: 'dataMin+50',
    }));
  }

  render() {
    const data = this.state.data;
    const left = this.state.left;
    const right = this.state.right;
    const refAreaLeft = this.state.refAreaLeft;
    const refAreaRight = this.state.refAreaRight;
    const top = this.state.top;
    const bottom = this.state.bottom;
    const top2 = this.state.top2;
    const bottom2 = this.state.bottom2;

    return (
      <div className="highlight-bar-charts" style={{ userSelect: 'none', width: '100%' }}>
        <button type="button" className="btn update" onClick={this.zoomOut.bind(this)}>
          Zoom Out
        </button>

        <ResponsiveContainer width="100%" height={240}>
          <LineChart
            width={800}
            height={250}
            data={data}
            onMouseDown={(e) => this.setState({ refAreaLeft: e.activeLabel })}
            onMouseMove={(e) => this.state.refAreaLeft && this.setState({ refAreaRight: e.activeLabel })}
            onMouseUp={this.zoom.bind(this)}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis allowDataOverflow dataKey="name" domain={[left, right]} type="category" />
            <YAxis allowDataOverflow domain={[bottom, top]} type="number" yAxisId="1" />
            <YAxis orientation="right" allowDataOverflow domain={[bottom2, top2]} type="number" yAxisId="2" />
            <Tooltip content={<CustomTooltip />} />
            <Line yAxisId="1" type="natural" dataKey="gdp" stroke="#8884d8" animationDuration={300} />
            <Line yAxisId="2" type="natural" dataKey="inflation" stroke="#82ca9d" animationDuration={300} />
            <Line yAxisId="2" type="natural" dataKey="unemployment" stroke="#ff7300" animationDuration={300} />
            <Line yAxisId="2" type="natural" dataKey="consumerSpending" stroke="#387908" animationDuration={300} />

            {refAreaLeft && refAreaRight ? (
              <ReferenceArea yAxisId="1" x1={refAreaLeft} x2={refAreaRight} strokeOpacity={0.3} />
            ) : null}
          </LineChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
